
.App-header {
  background-image: linear-gradient(to right, #f6d365, #fda085);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  
  font-size: calc(10px + 2vmin);
  color: rgb(223, 150, 15);
}
.Heading{
  background-color: rgba(166, 166, 31, 0.468);
  min-height: 10vh;
  font-size: 5vh;
  text-align: center;
  color: rgba(0, 0, 0, 0.468);
  background-image: linear-gradient(to right, #5a3a3a, #fda085);
  background-size: 200% 100%; /* double width to allow sliding */
  background-position: 100% 0;
animation: Heading-Banner 1s linear ;
}
.task-container{

  min-height: 20vh;
  font-size: 5vh;
  text-align: center;
  color: rgba(0, 0, 0, 0.468);
} 
.button{
  margin-top: 10px;
  margin-bottom: none;
  border: 4px ridge rgba(211, 220, 50, 0.6);
  font-size: large;
}
.button:hover{
  background: rgba(0, 0, 0, 0.6);
  color: rgb(230, 12, 12);
  border: 3px ridge rgba(142, 147, 54, 0.6);
  transition: all 0.3s ease-out;
  cursor: pointer;
  transform: scale(1.1);
}
.button:active{
  background: rgba(213, 65, 65, 0.6);
  color: rgb(255, 0, 0);
  border: 1px ridge rgba(0, 0, 0, 0.6);

  transition: all 0.5s ease-out;
  cursor: pointer;
  transform: scale(1.5);
}
@keyframes Heading-Banner{
  0% {
    background-position: 100% 0;
  }
   50% {
    background-position: 0% 0%;
  }
  100% {
    background-position: -100% 0;
  }
}
.task-completed{
  animation: Task-completed 1s linear ;
}

@keyframes Task-completed{
  0%{
    transform: scale(1);
  }
  50%{
    transform: scale(1.1);
    color: #03ff07;
  }
  100%{
    transform: scale(1);
    color:rgb(rgb(60, 255, 0));
  }
}
